.cart-dropdown {
    position: absolute;
    width: 240px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 55px;
    right: 40px;
    z-index: 5;
    .cart-items {
      height: 240px;
      display: flex;
      flex-direction: column;
      overflow-y: scroll;
      .empty{
        align-self: center;
      }
    }
  
    button {
      margin-top: auto;
    }
  }