.checkout-item{
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  align-items: center;
  .image-container {
      width: 22%;
      img {
        width: 100%;
        height: 100%;
      }
    }
   .quantity{
     display: flex;
     align-items: center;
     justify-content: center; 
  .cartModifier{
    font-size: 25px;
    &:hover{
      color: yellowgreen;
      cursor: pointer;
    }
  }}
}

@media(min-width:600px){
  .checkout-item{
  .name,.quantity,.price,.remove{
      text-align: center;
      width: 25%;
  }
  .quantity{
      padding-left:150px ;
  }
  .price{
      padding-left:140px ;
  }
  .remove{
      text-align: right;
  }}
}
